import {GridStack, GridStackNode} from "gridstack";
import Manager from "./cards/Manager";

const opts = {
    animate: false, // show immediate (animate: true is nice for user dragging though)
    float: false,
    margin: '0.75rem',
    staticGrid: true,
    cellHeight: '8rem',
    columnOpts: {
        breakpoints: [{w:768, c:1}],
    },
};

function calcColumnCount(): number {
    const width = $('#content').innerWidth();

    if (typeof width === "undefined" || width < 768) {
        return 1;
    }

    if (width < 1080) {
        return 8;
    }

    return 12;
}

export default class Dashboard {
    private readonly grid: GridStack;
    private readonly manager: Manager;

    constructor() {
        this.grid = GridStack.init(opts);

        this.resizeGrid();
        $(window).on('resize', () => this.resizeGrid());

        this.manager = new Manager();
        this.manager.registerActionMenuEvents().load().refreshAll();

        $('.refresh-all, .update-filtering').on('click', () => this.manager.refreshAll());
    }

    getGridStack(): GridStack {
        return this.grid;
    }

    getManager(): Manager {
        return this.manager;
    }

    private resizeGrid() {
        this.grid.column(calcColumnCount(), function (column: number, prevColumn: number, newNodes: GridStackNode[], nodes: GridStackNode[]) {
            nodes.forEach(node => {
                if (typeof node.x !== "undefined") {
                    node.x = column === 1 ? 0 : (node.x >= column ? node.x - column : node.x);
                }

                if (typeof node.w !== "undefined") {
                    node.w = (column === 1 || prevColumn === 1) ? 1 : (column < 12 && node.w > 4 ? 8 : node.w);
                }

                newNodes.push(node);
            });
        });
    }
}
