import Card from "./Card";

class HtmlCard extends Card {
    refresh (): JQueryPromise<any> {
        this.toggleLoading(true)

        return $.get(this.$elm.data('route'), this.filters.all())
            .done((response) => this.$elm.find('.html-card').html(response.data))
            .always(() => this.toggleLoading(false));
    }

    private toggleLoading(show: boolean): void {
        this.$elm.find('.loading').toggle(show);
        this.$elm.find('.html-card').toggle(! show);
    }
}

export default HtmlCard;
