import Chart from 'chart.js/auto';
import 'chartjs-adapter-luxon';
import UnderlyingData from "../utils/UnderlyingData.js";
import {cssvar, obj_get, url, wordwrap} from "../utils/utils.js";
import Card from "./Card";
import {Chart as ChartType, ChartEvent, ActiveElement, ChartOptions} from "chart.js/dist/types";
import {Scale} from "chart.js";
import { ChoroplethController, GeoFeature, ColorScale, ProjectionScale } from 'chartjs-chart-geo';
import {EmptyChartPlugin} from "./chart/EmptyChartPlugin";

// register controller in chart.js and ensure the defaults are set
Chart.register(ChoroplethController, GeoFeature, ColorScale, ProjectionScale);
Chart.register(EmptyChartPlugin);

// https://www.chartjs.org/docs/latest/general/colors.html#default-colors
Chart.defaults.color = cssvar('--color-text-primary');

class ChartCard extends Card {
    private chart: ChartType | null = null;

    refresh (): JQueryPromise<any> {
        const instance = this;

        return this.fetchData(function (response) {
            if (instance.chart === null) {
                instance.chart = new Chart(instance.$elm.find('canvas')[0], {
                    type: response.data.type,
                    data: response.data.data,
                    options: instance.options(response.data.options)
                });
            } else {
                instance.chart.data = response.data.data;
                instance.chart.options = instance.options(response.data.options);
                instance.chart.update();
            }

            // Bit of a hack to force the chart to the correct size.
            const { width, height } = instance.$elm.find('.chart-card')[0].getBoundingClientRect();
            instance.chart.resize(width, height);
        })
    }

    print(): void {
        super.setOptions({animation: false});
        super.print();
    }

    private clickHandler (evt: ChartEvent, elements: ActiveElement[], chart: ChartType): void {
        if (! super.implementsUnderlyingData() || evt.native === null) {
            return;
        }

        const points = chart.getElementsAtEventForMode(evt.native, 'nearest', { intersect: true }, true);
        if (points.length === 0) {
            return;
        }

        const firstPoint = points[0];
        const label = chart.data.labels?.[firstPoint.index] || chart.data.datasets[firstPoint.datasetIndex].label;
        const value = obj_get(chart.data.datasets[firstPoint.datasetIndex].data[firstPoint.index], super.underlyingDataValueKey());
        const route = url(super.underlyingDataRoute(), $.extend({label: label, value: value}, this.filters.all()));
        const exportRoute = url(super.underlyingDataExportRoute(), $.extend({label: label, value: value}, this.filters.all()));

        UnderlyingData.show(route, exportRoute);
    };

    private hoverHandler (evt: ChartEvent, elements: ActiveElement[], chart: ChartType): void {
        if (! super.implementsUnderlyingData() || evt.native === null) {
            return;
        }

        const points = chart.getElementsAtEventForMode(evt.native, 'index', { intersect: true }, false);
        (evt.native.target as HTMLElement).style.cursor = points.length ? 'pointer' : 'default';
    }

    private fetchData (callback: JQuery.Deferred.Callback<any>): JQueryPromise<any> {
        this.toggleLoading(true);

        return $.get(this.$elm.data('route'), this.filters.all())
            .done(callback)
            .always(() => this.toggleLoading(false));
    }

    private options (opts: ChartOptions): ChartOptions {
        const defaults = {
            scales: {
                x: {
                    display: 'auto',
                    ticks: {
                        callback: function (this: Scale, val: number): string[] {
                            const label = this.getLabelForValue(val);

                            return wordwrap(label.length > 30 ? `${label.substring(0, 30)}...` : label, 15, true);
                        }
                    }
                },
                y: {
                    display: 'auto',
                    ticks: {
                        callback: function (this: Scale, val: number): string[] {
                            const label = this.getLabelForValue(val);

                            return wordwrap(label.length > 30 ? `${label.substring(0, 30)}...` : label, 15, true);
                        }
                    }
                }
            },
            responsive: true,
            plugins: {
                legend: {
                    position: 'top',
                },
                tooltip: {
                    position: 'nearest',
                }
            },
            interaction: {
                intersect: false,
                mode: 'nearest',
            },
            onClick: this.clickHandler.bind(this),
            onHover: this.hoverHandler.bind(this),
        };

        return $.extend(true, {}, defaults, opts, this.opts);
    }

    private toggleLoading(show: boolean): void {
        this.$elm.find('.fa-spinner').toggle(show);
        this.$elm.find('canvas').toggle(! show);
    }
}

export default ChartCard;
