import Card from "./Card";

class NumberCard extends Card {
    refresh (): JQueryPromise<any> {
        this.toggleLoading(true)

        return $.get(this.$elm.data('route'), this.filters.all())
            .done((response) => this.$elm.find('.number-value').html(response.data || $('<span>').addClass('sp-description sp-text-2xl').text(Lang.get('report.no_data_found'))))
            .always(() => this.toggleLoading(false));
    }

    private toggleLoading(show: boolean): void {
        this.$elm.find('.number-card .fa-spinner').toggle(show);
        this.$elm.find('.number-card .number-value').toggle(! show);
    }
}

export default NumberCard;
