import {Chart as ChartType} from "chart.js/dist/types";
import {cssvar} from "../../utils/utils";

export const EmptyChartPlugin = {
    id: 'emptyChart',
    afterDraw(chart: ChartType) {
        const { datasets } = chart.data;
        let hasData = false;

        for (let dataset of datasets) {
            if ((dataset.data.length > 0 && dataset.data.some(item => item !== 0))
                || (typeof dataset.data === 'object' && Object.keys(dataset.data).length && Object.values(dataset.data).some(item => item !== 0))
            ) {
                hasData = true;
                break;
            }
        }

        const ctx = chart.ctx;
        if (! hasData && ctx) {
            const line1 = Lang.get('report.no_data_found'),
                  line2 = Lang.get('report.adjust_time_range'),
                  line1X = (chart.width / 2) - (ctx.measureText(line1).width / 2),
                  line1Y = chart.height / 2,
                  line2X = (chart.width / 2) - (ctx.measureText(line2).width / 2),
                  line2Y = (chart.height / 2) + 14;

            if (typeof chart.options.plugins === 'undefined') {
                chart.options.plugins = {tooltip: {enabled: false}};
            }

            if (typeof chart.options.plugins.tooltip === 'undefined') {
                chart.options.plugins.tooltip = {enabled: false};
            }

            chart.options.plugins.tooltip.enabled = false;
            chart.update();

            ctx.save();
            ctx.fillStyle = cssvar('--color-text-primary');
            ctx.fillText(line1, line1X, line1Y);
            ctx.fillText(line2, line2X, line2Y);
            ctx.restore();
        }
    }
};
