import Filters from "../filtering/Filters";
import Card from "./Card";
import UnderlyingData from "../utils/UnderlyingData";
import {url} from "../utils/utils";

import { Api } from 'datatables.net';

import DataTable from 'datatables.net-dt';
import 'datatables.net-responsive-dt';
import './../../../libs/datatable/pagination/input.js';
import './../../../libs/datatable/pagination/simple.js';
import './../../../libs/datatable/config.js';

// https://datatables.net/blog/2022-11-10-esm#Extending-the-API-definitions
declare module 'datatables.net' {
    interface Api<T> {
        reloadAsync(): JQueryPromise<any>;
    }
}

class TableCard extends Card {
    private table: Api<any> | null = null;

    constructor(filters: Filters, $elm: JQuery) {
        super(filters, $elm);

        this.registerDataTableApis();
        this.registerListeners();
    }

    refresh (): JQueryPromise<any> {
        if (this.table === null) {
            const $tableCard = this.$elm.find('.table-card');
            this.table = new DataTable(
                '#' + $tableCard.data('id'),
                $.extend(true, {}, $tableCard.data('options'), this.opts)
            );
            this.$elm.find('.table-card, .loading').toggle();
        }

        return this.table.reloadAsync();
    };

    print(): void {
        super.setOptions({pageLength: 100});
        super.print();
    }

    private registerDataTableApis (): void {
        $.fn.dataTable.Api.register('reloadAsync', function (this: Api<any>, resetPaging: boolean) {
            const dfd = $.Deferred();
            this.ajax.reload((json: any) => dfd.resolve(json), resetPaging);

            return dfd.promise();
        });
    }

    private registerListeners (): void {
        const instance: this = this;
        this.$elm.find('.sp-dataTable').on('preXhr.dt', function (e, settings, data) {
            $.extend(data, instance.filters.all());
        });

        this.$elm.find('.sp-dataTable').on('click', '.underlying-drill-in', function () {
            UnderlyingData.show(
                url($(this).data('underlyingView'), instance.filters.all()),
                url($(this).data('underlyingExport'), instance.filters.all()),
            );
        });
    };
}

export default TableCard;
