import Swal from "@supportpal/sweetalert2";

function UnderlyingData () {}

UnderlyingData.show = function (route: string, exportRoute: string): void {
    Swal.showLoading();

    $.get(route)
        .done(function (response) {
            Swal.fire({
                title: Lang.get('report.underlying_data'),
                html: response.data.table,
                width: '97%',
                showCloseButton: true,
                showConfirmButton: true,
                showLoaderOnConfirm: true,
                confirmButtonText: Lang.get('report.export_csv'),
                didOpen: () => {
                    (Swal.getHtmlContainer() as HTMLElement).style.textAlign = 'left';
                    $('#' + response.data.id).dataTable(JSON.parse(response.data.options));
                },
                preConfirm: function () {
                    return $.get(exportRoute)
                        .then(function(response) {
                            if (response.status == 'success') {
                                return response;
                            }

                            throw new Error(response.statusText);
                        })
                        .catch(function () {
                            Swal.fire(Lang.get('messages.error'), Lang.get('report.export_error'), 'error');

                            return false;
                        });
                }
            }).then(function (result) {
                if (result.value) {
                    Swal.fire(Lang.get('messages.success'), Lang.get('report.export_scheduled'), 'success');
                }
            });
        });
}

export default UnderlyingData;
