import Timeframe from "./Timeframe";

interface Condition {
    name: string,
    value: string
}

interface Data {
    [key: string]: any;
}

class Filters {
    private timeframe: Timeframe;
    private data: Data = {};

    constructor(timeframe: Timeframe) {
        this.timeframe = timeframe;
    }

    setData(data: Data) {
        this.data = data;
    }

    all(): Data {
        let data: Data = Object.create(this.data);

        if (this.timeframe.getStartTimestamp() !== undefined) {
            data.startTimestamp = this.timeframe.getStartTimestamp();
        }

        if (this.timeframe.getEndTimestamp() !== undefined) {
            data.endTimestamp = this.timeframe.getEndTimestamp();
        }

        $.each($('.sp-report-filtering :input').serializeArray(), function(index: number, value: Condition): void {
            data[value.name] = value.value;
        });

        return data;
    };
}

export default Filters;
