/*
 * Modified from:
 *  - https://cdn.datatables.net/plug-ins/2.2.1/features/inputPaging/dist/dataTables.inputPaging.js
 *  - https://github.com/DataTables/DataTables/pull/60#issuecomment-566641518
 */
var DataTable = $.fn.dataTable;
var tags = {
    wrapper: {
        tag: 'div',
        className: 'dt-inputpaging dt-paging',
    },
    item: {
        tag: 'button',
        className: 'dt-paging-button',
        disabled: 'disabled',
    },
};

DataTable.feature.register('noTotalPaging', function (settings, opts) {
    let api = new DataTable.Api(settings);

    // Create the DOM elements for the paging control
    let wrapper = createElement(tags.wrapper, undefined, undefined);
    let previous = createElement(
        tags.item,
        api.i18n('oPaginate.sPrevious', '\u2039'),
        () => api.page('previous').draw(false)
    );
    let next = createElement(
        tags.item,
        api.i18n('oPaginate.sNext', '\u203A'),
        () => api.page('next').draw(false)
    );

    // Assemble the DOM structure
    wrapper.appendChild(previous);
    wrapper.appendChild(next);

    let hasMoreRecords = false;
    api.on('draw', () => {
        let info = api.page.info();

        // Update the classes for the "jump" buttons to show what is available
        setState(previous, tags.item.disabled, info.page === 0);
        setState(next, tags.item.disabled, ! hasMoreRecords);
    });

    // Fetch one more record than we want to display, so we know whether there's a next page.
    api.on('preXhr.dt', function ( e, settings, data ) {
        data.length += 1;
    });

    api.on('xhr.dt', function ( e, settings, json, xhr ) {
        json.recordsTotal = json.recordsFiltered = Number.MAX_SAFE_INTEGER;

        // We ask for len + 1, so if aaData length is greater than the display length, then remove the last
        // record and pretend we never saw it. We don't want to display it in the table. This is only used
        // to determine whether we can display the next page or not.
        hasMoreRecords = json.data.length > api.page.info().length;
        if (hasMoreRecords) {
            json.data.pop();
        }
    });

    return wrapper;
});

function setState(el, disabledClass, disabled) {
    el.classList.toggle(disabledClass, disabled);
    if (disabled) {
        el.setAttribute('disabled', 'disabled');
    } else {
        el.removeAttribute('disabled');
    }
}

/**
 * Create a new element
 *
 * @param opts Tag and class name
 * @param text Text to show in the element
 * @param fn Click event handler
 * @returns Element
 */
function createElement(opts, text, fn) {
    let el = document.createElement(opts.tag);
    el.className = opts.className;

    if (text) {
        el.innerHTML = text;
    }

    // Top level only
    if (fn) {
        el.addEventListener('click', fn);
    }

    return el;
}
