import "moment";
import "daterangepicker";
import "./../../../../general/js/daterangepicker.js"

class Timeframe {
    private startTimestamp: number | undefined;
    private endTimestamp: number | undefined;
    private callback: Function | null = null;

    constructor() {
        this.initDatePicker();
        this.registerListeners();
        this.select($('.sp-report-timeframe > .sp-option.default'));
    }

    onSelect (c: Function): void {
        this.callback = c;
    };

    setStartTimestamp(timestamp: number): void {
        this.startTimestamp = timestamp;
    }

    getStartTimestamp (): number|undefined {
        return this.startTimestamp;
    };

    setEndTimestamp(timestamp: number): void {
        this.endTimestamp = timestamp;
    }

    getEndTimestamp (): number|undefined {
        return this.endTimestamp;
    };

    private initDatePicker (): void {
        const instance = this,
            $elm = $('.sp-filter-datepicker'),
            opts = {
                autoApply: false,
            };

        $elm.daterangepicker(opts);
        $elm.on('apply.daterangepicker', function (ev, picker) {
            $('.sp-report-timeframe > .sp-option.relative').removeClass('xl:sp-inline-block');
            $('.sp-report-timeframe .sp-dropdown-container').addClass('sp-hidden');
            $('.sp-filter-datepicker.custom').addClass('selected');
            $('.sp-report-timeframe').find('.sp-filter-datepicker-text-wrapper')
                .addClass('sm:sp-inline-block')
                .find('.sp-filter-datepicker-text')
                .text(picker.startDate.formatSupportPal() + Lang.get('general.range_separator') + picker.endDate.formatSupportPal());

            instance.runCallback(picker.startDate.unix(), picker.endDate.unix());
        });
        $elm.on('cancel.daterangepicker', function (ev, picker) {
            $('.sp-report-timeframe > .sp-option.relative').addClass('xl:sp-inline-block');
            $(' .sp-report-timeframe .sp-dropdown-container').removeClass('sp-hidden');
            $('.sp-filter-datepicker.custom').removeClass('selected');
            $('.sp-report-timeframe').find('.sp-filter-datepicker-text-wrapper')
                .removeClass('sm:sp-inline-block')
                .find('.sp-filter-datepicker-text').text('');
            instance.select($('.sp-report-timeframe > .sp-option.default'));
        });
    };

    private registerListeners (): void {
        const instance = this;
        $('.sp-report-timeframe .sp-option.relative').on('click', function () {
            instance.select($(this));
        });
    };

    private select ($elm: JQuery): void {
        var time = $elm.text().trim();

        $('.sp-report-timeframe').find('.sp-option.selected:not(.sp-action)').removeClass('selected');
        $('.sp-report-timeframe').find('.sp-option[data-time=' + time + ']').addClass('selected');
        $('.sp-timeframe-selected').text(time);
        this.runCallback($elm.data('start-time'), $elm.data('end-time'));
    }

    private runCallback (s: number, e: number): void {
        this.startTimestamp = s;
        this.endTimestamp = e;

        if (this.callback !== null) {
            this.callback.call(this);
        }
    };
}

export default Timeframe;
