interface GenericObject {
    [key: string]: any;
}

function cssvar(name: string): string
{
    return window.getComputedStyle(document.documentElement).getPropertyValue(name);
}

function url(url: string, params: GenericObject)
{
    const hasParams = typeof (new URL(url)).searchParams.entries().next().value !== 'undefined';

    return url + (hasParams ? '&' : '?') + $.param(params);
}

function wordwrap( str: string, width: number, cut: boolean ): string[] {
    width = width || 75;
    cut = cut || false;

    if (! str) {
        return [str];
    }

    const regex = '.{1,' +width+ '}(\\s|$)' + (cut ? '|.{' +width+ '}|.+$' : '|\\S+?(\\s|$)');
    const matches = str.match(RegExp(regex, 'g'));
    if (matches === null) {
        return [str];
    }

    return Array.from(matches.values());
}

function obj_get(obj: any, key: string): any {
    if (key.length === 0) {
        return obj;
    }

    return key.split('.').reduce((a, b) => a[b], obj);
}

export {cssvar, url, wordwrap, obj_get};
