import NumberCard from "./NumberCard.js";
import Timeframe from "../filtering/Timeframe.js";
import Filters from "../filtering/Filters.js";
import TableCard from "./TableCard.js";
import ChartCard from "./ChartCard.js";
import HtmlCard from "./HtmlCard";
import {url} from "../utils/utils.js";
import UnderlyingData from "../utils/UnderlyingData.js";
import Card from "./Card";
import Swal from "@supportpal/sweetalert2";

interface Constructable<T> {
    new(...args: any) : T;
}

interface Components {
    number: Constructable<NumberCard>,
    table: Constructable<TableCard>,
    chart: Constructable<ChartCard>,
    html: Constructable<HtmlCard>,
}

interface FilterData {
    [key: string]: any;
}

interface Cards {
    [key: string]: Card,
}

/**
 * Maps to component() in PHP Card class.
 */
const ComponentMap: Components = {
    'number': NumberCard,
    'table': TableCard,
    'chart': ChartCard,
    'html': HtmlCard
};

function initTimeframe(manager: Manager): Timeframe {
    const t = new Timeframe();
    t.onSelect(function () {
        manager.refreshAll();
    })

    return t;
}

class Manager {
    cards: Cards = {};
    timeframe: Timeframe = initTimeframe(this);
    filters: Filters = new Filters(this.timeframe);

    initFiltersFromUrl(): this {
        const params = new URLSearchParams(window.location.search);
        const start = params.get('startTimestamp');
        if (start !== null) {
            this.timeframe.setStartTimestamp(parseInt(start));
        }

        const end = params.get('endTimestamp');
        if (end !== null) {
            this.timeframe.setEndTimestamp(parseInt(end));
        }

        let filters: FilterData = {};
        params.forEach((value, key) => {
            if (key.startsWith('conditions[')) {
                filters[key] = value;
            }
        });

        this.filters.setData(filters);

        return this;
    }

    load(): this {
        const instance = this;
        $('.grid-stack-item').each(function () {
            const id: string = $(this).data('id');
            const component: keyof typeof ComponentMap = $(this).data('component');
            instance.cards[id] = new ComponentMap[component](instance.filters, $(this));
        });

        return instance;
    }

    registerActionMenuEvents(): this {
        const instance = this;
        $('.refresh-btn').on('click', function () {
            const id = $(this).parents('.grid-stack-item').data('id');
            instance.get(id).refresh();
        });

        $('.export-csv-btn').on('click', function () {
            Swal.showLoading();

            const $item = $(this).parents('.grid-stack-item');
            const route = url($item.data('downloadCsvRoute'), instance.filters.all());
            $.get(route).done(() => Swal.fire(Lang.get('messages.success'), Lang.get('report.export_scheduled'), 'success'));
        });

        $('.print-btn').on('click', function () {
            const $item = $(this).parents('.grid-stack-item');
            const route = url($item.data('printRoute'), instance.filters.all());
            window.open(route, '_blank');
        });

        $('.underlying-btn').on('click', function () {
            const $item = $(this).parents('.grid-stack-item');
            UnderlyingData.show(
                url($item.data('underlyingView'), instance.filters.all()),
                url($item.data('underlyingExport'), instance.filters.all())
            );
        });

        return instance;
    }

    first(): Card {
        for (const id in this.cards) {
            if (this.cards.hasOwnProperty(id)) {
                return this.cards[id];
            }
        }

        throw Error('There are no cards.');
    }

    get (id: string): Card {
        if (typeof this.cards[id] === 'undefined') {
            throw Error('Card of id \'' + id + '\' is not defined.');
        }

        return this.cards[id];
    };

    refreshAll (): void {
        const $refreshBtn = $('.refresh-all').find('i');
        const $dashboardActions = $('.sp-dashboard-actions').find('button');
        $refreshBtn.addClass('fa-spin');
        $dashboardActions.prop('disabled', true);

        const promises: JQueryPromise<any>[] = [];
        $.each(this.cards, (id: string, card: Card) => promises.push(card.refresh()));

        $.when.apply($, promises).then(function (): void {
            $refreshBtn.removeClass('fa-spin');
            $dashboardActions.prop('disabled', false);
        });
    };
}

export default Manager;
